:root {
  --bg: #0b0d0f;
  --accent: #15a574;
  --muted: #bfc7c3;
  --card: #0f1314;
  --glass: #ffffff0a;
}

* {
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

body {
  background: var(--bg);
  color: #eef2f1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  font-family: Poppins, system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, Arial;
  line-height: 1.45;
}

.container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 18px;
}

.hero {
  justify-content: center;
  align-items: center;
  min-height: 70vh;
  padding: 48px 0 64px;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  overflow: hidden;
}

.hero-bg {
  filter: brightness(.55) contrast(.95);
  will-change: transform;
  background-image: url("background.250e49cc.jpg");
  background-position: center;
  background-size: cover;
  transition: transform 10s linear;
  position: absolute;
  inset: 0;
  transform: scale(1.06);
}

.hero:hover .hero-bg {
  transform: scale(1.03);
}

.hero-overlay {
  background: linear-gradient(#050a0859, #03060599);
  position: absolute;
  inset: 0;
}

.brand {
  justify-content: space-between;
  align-items: center;
  gap: 16px;
  width: 100%;
  margin-bottom: 18px;
  display: -ms-flexbox;
  display: flex;
}

.logo {
  width: 120px;
  display: block;
}

.top-actions {
  margin-left: auto;
}

.top-actions .btn-ghost {
  color: var(--muted);
  background: none;
  border: 1px solid #ffffff1f;
  border-radius: 8px;
  padding: 8px 14px;
  font-weight: 600;
  text-decoration: none;
}

.hero-inner {
  z-index: 2;
  text-align: center;
  padding: 20px 8px;
  position: relative;
}

.headline {
  letter-spacing: -.01em;
  margin-bottom: 12px;
  font-size: 2.4rem;
  line-height: 1.05;
}

.sub {
  color: var(--muted);
  margin-bottom: 20px;
  font-size: 1.05rem;
}

.hero-ctas {
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  justify-content: center;
  gap: 14px;
  display: -ms-flexbox;
  display: flex;
}

.btn {
  border-radius: 10px;
  padding: 12px 22px;
  font-weight: 600;
  text-decoration: none;
  display: inline-block;
}

.btn-primary {
  background: var(--accent);
  color: #fff;
  transition: transform .18s, box-shadow .18s;
  box-shadow: 0 6px 18px #15a5741f;
}

.btn-primary:hover {
  transform: translateY(-4px);
  box-shadow: 0 14px 30px #15a57429;
}

.btn-outline {
  color: #fff;
  background: none;
  border: 2px solid #ffffff14;
}

.section {
  padding: 56px 0;
}

.section-title {
  color: #fff;
  margin-bottom: 20px;
  font-size: 1.6rem;
}

.grid-4 {
  grid-template-columns: repeat(4, 1fr);
  gap: 18px;
  display: grid;
}

.grid-3 {
  grid-template-columns: repeat(3, 1fr);
  gap: 18px;
  display: grid;
}

.grid-2 {
  grid-template-columns: repeat(2, 1fr);
  gap: 18px;
  display: grid;
}

.card {
  background: var(--card);
  text-align: left;
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 6px 18px #02060599;
}

.card .icon {
  margin-bottom: 10px;
  font-size: 28px;
}

.card h3 {
  margin: 0 0 8px;
  font-size: 1.05rem;
}

.card p {
  color: var(--muted);
  margin: 0;
  font-size: .95rem;
}

.cat-card {
  color: inherit;
  background: linear-gradient(#0000002e, #00000038);
  border-radius: 12px;
  text-decoration: none;
  display: block;
  overflow: hidden;
  box-shadow: 0 10px 30px #00000073;
}

.cat-img {
  background-position: center;
  background-size: cover;
  height: 160px;
}

.cat-card h3 {
  margin: 0;
  padding: 16px 16px 6px;
}

.cat-card p {
  color: var(--muted);
  margin: 0;
  padding: 0 16px 16px;
}

.reviews-row {
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  gap: 18px;
  display: -ms-flexbox;
  display: flex;
}

.review {
  background: linear-gradient(#ffffff05, #ffffff03);
  border-radius: 10px;
  -ms-flex: 1;
  flex: 1;
  min-width: 220px;
  padding: 18px;
}

.review p {
  color: #e9efe9;
  margin-bottom: 10px;
  font-style: italic;
}

.review footer {
  color: var(--muted);
  font-size: .9rem;
}

.order-inner {
  grid-template-columns: 1fr 360px;
  align-items: start;
  gap: 28px;
  display: grid;
}

.order-left {
  background: var(--glass);
  backdrop-filter: blur(6px);
  border-radius: 12px;
  padding: 20px;
}

.lead-form label {
  color: var(--muted);
  margin-bottom: 12px;
  font-size: .95rem;
  display: block;
}

.lead-form input, .lead-form textarea {
  color: #fff;
  background: none;
  border: 1px solid #ffffff0f;
  border-radius: 8px;
  width: 100%;
  padding: 10px;
}

.form-actions {
  align-items: center;
  gap: 12px;
  margin-top: 8px;
  display: -ms-flexbox;
  display: flex;
}

.btn-ghost {
  color: #fff;
  background: none;
  border: 1px solid #ffffff14;
  border-radius: 8px;
  padding: 10px 14px;
  text-decoration: none;
}

.socials-small a {
  color: var(--muted);
  margin-right: 12px;
  text-decoration: none;
  display: inline-block;
}

.footer {
  color: var(--muted);
  background: #070808;
  border-top: 1px solid #ffffff05;
  padding: 18px 0;
}

.footer-inner {
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  display: -ms-flexbox;
  display: flex;
}

@media (width <= 980px) {
  .grid-4, .grid-3 {
    grid-template-columns: repeat(2, 1fr);
  }

  .order-inner {
    grid-template-columns: 1fr;
  }

  .hero {
    min-height: 60vh;
  }
}

@media (width <= 560px) {
  .grid-4, .grid-3 {
    grid-template-columns: 1fr;
  }

  .headline {
    font-size: 1.45rem;
  }

  .logo {
    width: 92px;
  }

  .top-actions {
    display: none;
  }
}

.anim-up {
  opacity: 0;
  animation: .6s forwards up;
  transform: translateY(18px);
}

@keyframes up {
  to {
    opacity: 1;
    transform: none;
  }
}

.cat-1 .cat-img {
  background-image: url("proyivovospalitelnoe.74bde6a3.jpg");
}

.cat-2 .cat-img {
  background-image: url("klischi.770961f4.jpg");
}

.cat-3 .cat-img {
  background-image: url("antihelment.64d1db9c.jpg");
}

.cat-img {
  background-position: center;
  background-size: cover;
  border-radius: 12px;
  width: 100%;
  height: 250px;
  transition: all .3s;
  box-shadow: 0 4px 12px #0000001a;
}

.cat-img:hover {
